import {
	reqPictureList
} from '@/network/projectReq'


export default {
	name: "Picture",
	data() {
		return {
			current: 0,
			picList: []

		}
	},
	props: {
		params: Object
	},
	created() {

	},
	components: {

	},
	watch: {
		params(newVal) {
			if (newVal) {
				this.getPictureList()
			}

		}
	},
	methods: {
		getPictureList() {
			reqPictureList(this.params.class_id).then(res => {
				this.picList = res.data
			})
		},

		swiperChange(e) {
			var index = e.target.current || e.detail.current;
			this.current = index;
		},
		toPrev(){
			if(this.current!==0){
				this.current -= 1
			}
		},
		toNext(){
			if(this.current!==this.picList.length-1){
				this.current += 1
			}
		}


	},
	computed: {
		curImage() {
			return (this.current + 1) + '/' + this.picList.length
		},
		leftArrowsOpacity(){
			return this.current === 0? 0.5: 1
		},
		rightArrowsOpacity(){
			return this.current === this.picList.length-1? 0.7: 1
		}
	}

}
